@import "colors";
%titel{
    text-decoration: underline solid $princepal-color;
    font-size: 40px;
    color: $princepal-color;
    margin-top: 30px;
}

%text{
    font-size: 25px;
    margin-top: 25px;
}

*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    background-color: $back-color;
}

.container{
    color: #fff;
    align-items: center;
    padding: 30px 10%;

    &-titel{
        width: 100%;
        h1{
            margin: 10px 50px 10px 600px;
            text-decoration: underline solid $princepal-color;
            font-size: 60px;
            color: $princepal-color;
        }
    }

    &-serveces{
            margin: 100px 10px 10px 10px;
        &-first_servis{
            h1{
                @extend %titel;
            }

            h3{
                @extend %text;
            }
        }

        &-second_servis{
           h1{
            @extend %titel;
           }

           h3{
            @extend %text;
           }
        }

        &-therd_servis{
            h1{
                @extend %titel;
            }

            h3{
                @extend %text;
            }
        }
    }
}